.weoffer {
  padding-bottom: 60px;

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 0 105px;
  }

  &__content {
    grid-column: 5 span;
  }

  &__title {
    font-family: 'Lato';
    text-align: left;
    margin-bottom: 60px;
    font-weight: 900;
  }
}