.modal {
    &__container {
        width: 486px;
        margin: auto;
        padding: 48px;

        border: 1px solid var(--gray-300);
        border-radius: var(--border-radius);
        box-shadow: 0 4px 4px -4px rgba(30, 33, 44, 0.05), 0 12px 10px -6px rgba(30, 33, 44, 0.08), 0 26px 24px -10px rgba(30, 33, 44, 0.1), 0 60px 80px -20px rgba(30, 33, 44, 0.16);
        background: var(--white);
        @include transition ($transition-property: scale);
    }

    &__close {
        position: absolute;
        right: 24px;
        top: 24px;
        width: 24px;
        height: 24px;
        background-image: none;


        &-icon {
            display: flex;
            color: var(--gray-700);
            @include transition ($transition-property: color);
            cursor: pointer;
        }


        // &:focus {
        //     outline: 2px solid var(--primary);
        //     outline-offset: 2px;
        // }

        @media (any-hover: hover) {
            &:hover &-icon {
                color: var(--primary);
            }
        }
    }
}