  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.offices__link:focus {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}
@media (any-hover: hover) {
  .offices__link:hover {
    color: var(--primary);
  }
}
.offices__link:active {
  color: var(--primary);
}
.offices__link--map {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: var(--primary);
}
.offices__link--name {
  color: var(--gray-700);
}
.offices__social {
  padding-top: 120px;
}
.offices__social-title {
  margin-bottom: 40px;
}
.offices__social .social {
  justify-content: center;
  gap: 24px;
}
@media screen and (width < 1025.01px) {
  .offices__nav-coll:not(:last-child)::after {
    display: none;
  }
}
@media screen and (width < 768.01px) {
  .offices__nav-coll {
    grid-column: 12 span;
    width: 100%;
    text-align: center;
  }
  .offices__nav-coll:not(:last-child) {
    margin-bottom: 30px;
  }
}